// LoginForm.js
import React from 'react';
import FormComponent from './FormComponent';

function LoginForm({ email, password, setEmail, setPassword, handleLogin, message }) {
  return (
    <FormComponent 
      title="Login"
      email={email}
      password={password}
      setEmail={setEmail}
      setPassword={setPassword}
      handleSubmit={handleLogin}
      message={message}
    />
  );
}

export default LoginForm;
