// RegistrationForm.js
import React from 'react';
import FormComponent from './FormComponent';

function RegistrationForm({ email, password, setEmail, setPassword, handleRegister, message }) {
  return (
    <FormComponent 
      title="Register"
      email={email}
      password={password}
      setEmail={setEmail}
      setPassword={setPassword}
      handleSubmit={handleRegister}
      message={message}
    />
  );
}

export default RegistrationForm;
